ul.post-list {
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    margin-bottom: 30px;
}
ul.post-list li {
    display: flex;
    flex-direction: column;
}
ul.post-list li header {
    width: 100%;
    height: 120px;
    background-size: cover;
    border-radius: 4px;
}
ul.post-list li strong {
    margin-top: 10px;
    font-size: 24px;
    color: #444;
}
ul.post-list li span {
    font-size: 15px;
    color: #999;
}
form {
    display: flex;
    flex-direction: column;
}
form input {
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 2px;
    height: 45px;
    padding: 0 15px;
    font-size: 16px;
}
form label {
    font-size: 14px;
    color: #444;
    font-weight: bold;
    margin-bottom: 8px;
}
button.btn {
    border: 0;
    border-radius: 2px;
    width: 100%;
    height: 42px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: bold;
    background: #7159c1;
    color: #fff;
    cursor: pointer;
}
h2 {
    color: white;
    margin-top: 20px;
}
